$navbar-background-color: v("scheme-main") !default;
$navbar-box-shadow-size: 0 2px 0 0 !default;
$navbar-box-shadow-color: v("background") !default;
$navbar-height: 3.25rem !default;
$navbar-padding-vertical: 1rem !default;
$navbar-padding-horizontal: 2rem !default;
$navbar-z: 30 !default;
$navbar-fixed-z: 30 !default;

$navbar-item-color: v("text") !default;
$navbar-item-hover-color: v("link") !default;
$navbar-item-hover-background-color: v("scheme-main-bis") !default;
$navbar-item-active-color: v("scheme-invert") !default;
$navbar-item-active-background-color: transparent !default;
$navbar-item-img-max-height: 1.75rem !default;

@include vRegister(navbar-item-color,$navbar-item-color);

$navbar-burger-color: v("navbar-item-color") !default;

$navbar-tab-hover-background-color: transparent !default;
$navbar-tab-hover-border-bottom-color: v("link") !default;
$navbar-tab-active-color: v("link") !default;
$navbar-tab-active-background-color: transparent !default;
$navbar-tab-active-border-bottom-color: v("link") !default;
$navbar-tab-active-border-bottom-style: solid !default;
$navbar-tab-active-border-bottom-width: 3px !default;

$navbar-dropdown-background-color: v("scheme-main") !default;
$navbar-dropdown-border-top: 2px solid v("border") !default;
$navbar-dropdown-offset: -4px !default;
$navbar-dropdown-arrow: v("link") !default;
$navbar-dropdown-radius: v("radius-large") !default;
$navbar-dropdown-z: 20 !default;

$navbar-dropdown-boxed-radius: v("radius-large") !default;
$navbar-dropdown-boxed-shadow: 0 8px 8px vAlphaChange("scheme-invert", 0.1), 0 0 0 1px vAlphaChange("scheme-invert", 0.1) !default;

$navbar-dropdown-item-hover-color: v("scheme-invert") !default;
$navbar-dropdown-item-hover-background-color: v("background") !default;
$navbar-dropdown-item-active-color: v("link") !default;
$navbar-dropdown-item-active-background-color: v("background") !default;

$navbar-divider-background-color: v("background") !default;
$navbar-divider-height: 2px !default;

$navbar-bottom-box-shadow-size: 0 -2px 0 0 !default;

$navbar-breakpoint: $desktop !default;

