@include vRegister(navbar-height,$navbar-height);
@include vRegister(navbar-z,$navbar-z);
@include vRegister(navbar-box-shadow-size,$navbar-box-shadow-size);
@include vRegister(navbar-box-shadow-color,$navbar-box-shadow-color);
@include vRegister(navbar-fixed-z,$navbar-fixed-z);
@include vRegister(navbar-bottom-box-shadow-size,$navbar-bottom-box-shadow-size);
@include vRegister(navbar-burger-color,$navbar-burger-color);
@include vRegister(navbar-item-hover-background-color,$navbar-item-hover-background-color);
