@include vRegister(navbar-item-img-max-height,$navbar-item-img-max-height);
@include vRegister(navbar-tab-hover-background-color,$navbar-tab-hover-background-color);
@include vRegister(navbar-tab-hover-border-bottom-color,$navbar-tab-hover-border-bottom-color);
@include vRegister(navbar-tab-active-background-color,$navbar-tab-active-background-color);
@include vRegister(navbar-tab-active-border-bottom-color,$navbar-tab-active-border-bottom-color);
@include vRegister(navbar-tab-active-border-bottom-style,$navbar-tab-active-border-bottom-style);
@include vRegister(navbar-tab-active-border-bottom-width,$navbar-tab-active-border-bottom-width);
@include vRegister(navbar-tab-active-color,$navbar-tab-active-color);
