@include selection {
  background-color: $primary;
  color: $primary-invert; }

.bd-post-container {
  margin: 0 auto;
  max-width: 56rem; }

.bd-post-fullwidth {
  text-align: center; }

@include from(60rem) {
  .bd-post-fullwidth {
    margin-left: calc(28rem - 50vw);
    margin-right: calc(28rem - 50vw); } }

.bd-post {
  .table {
    font-size: 1rem; }
  .bd-anchor-title {
    margin-top: 0 !important; } }

.bd-has-drawing {
  position: relative; }

.bd-drawing {
  display: none;
  pointer-events: none;
  position: absolute;
  &.bd-is-try-it-out {
    bottom: 100%;
    right: 100%; }
  &.bd-is-love-letters {
    bottom: 110%;
    right: 10%; }
  &.bd-is-crazy {
    left: -10%;
    top: 110%; }
  &.bd-is-customize {
    right: -10%;
    top: 105%; }
  &.bd-is-opinion-free {
    right: 100%;
    top: 110%; }
  &.bd-is-join-us {
    bottom: 80%;
    left: 100%; }
  &.bd-is-spam-free {
    bottom: 100%;
    right: 90%; } }

@include tablet {
  .bd-drawing {
    display: inline; } }

.bd-links {
  counter-reset: bd-links; }

.bd-link {
  border-radius: $radius-large;
  color: $text-light;
  display: block;
  font-size: $size-5;
  padding: 1rem 3rem 1.5rem 5rem;
  position: relative;
  transition-duration: $speed;
  transition-property: background-color, color;
  strong {
    font-weight: $weight-semibold; }
  &:hover {
    background-color: $scheme-main-bis; } }

.bd-link-surtitle {
  float: right;
  font-size: 0.75em;
  margin-bottom: 1em;
  margin-left: 2em;
  opacity: 0.5;
  padding-top: 0.25em; }

.bd-link-name {
  line-height: 1.25;
  margin-bottom: 0.25em;
  position: relative; }

.bd-link-figure {
  position: absolute;
  right: calc(100% + 0.75em);
  text-align: center;
  top: 0;
  min-width: 1.5em; }

.bd-link-counter {
  color: $link;
  display: block;
  counter-increment: bd-links;
  font-weight: $weight-normal;
  &::before {
    content: counter(bd-links); } }

.bd-link-icon {
  display: block;
  font-size: 2rem;
  width: 1.5em; }

.bd-link-more {
  font-size: 0.75em;
  opacity: 0.5; }

@include mobile {
  .bd-links {
    margin-left: -1.5rem;
    margin-right: -1.5rem; }
  .bd-link {
    border-radius: 0;
    padding-right: 1.5rem; } }

@include desktop {
  .bd-links {
    display: flex;
    flex-wrap: wrap; }
  .bd-link {
    width: 50%; } }

.bd-link-name {
  color: $text-strong;
  font-size: $size-4;
  font-weight: $weight-semibold; }

.bd-docs {
  display: flex;
  flex-wrap: wrap; }

.bd-doc {
  margin: 0 3rem 1.5rem 0;
  @include mobile {
    min-width: calc(50% - 3rem); }
  @include tablet {
    min-width: calc(33.3333% - 3rem); } }

.bd-doc-title {
  color: $text-strong;
  a {
    color: currentColor;
    &:hover {
      color: $link; } }
  &:not(:last-child) {
    margin-bottom: 0.75rem; } }

.bd-boxes {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.bd-box {
  border: 4px solid $background;
  border-radius: $radius; }

.bd-box-header {
  text-align: center; }

.bd-typo {
  text-align: center;
  &:not(:first-child) {
    margin-top: $main-spacing; } }

.bd-has-text-rss {
  color: $rss; }

.has-text-star {
  color: $star; }

