.bd-special-shadow {
  background-image: linear-gradient(rgba(#000, 0.1), rgba(#000, 0));
  height: 8px;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 100%;
  transform: scaleY(0);
  transform-origin: center top; }

@include touch {
  .bd-is-clipped-touch {
    overflow: hidden !important; } }

#images {
  tr {
    td:nth-child(2) {
      width: 320px; } } }

.bd-color {
  border-radius: 2px;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.1), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  display: inline-block;
  float: left;
  height: 24px;
  margin-right: 8px;
  width: 24px; }

.button.bd-is-rss {
  background-color: $rss;
  border-color: transparent;
  color: #fff;
  &:hover {
    background-color: darken($rss, 5%); }
  &:active {
    background-color: darken($rss, 10%); } }

.bd-view-all-versions {
  color: $text-light;
  &:hover {
    text-decoration: underline; } }

.bd-feature-title {
  color: $text-light;
  a {
    border-bottom: 1px solid transparent;
    color: $text-strong;
    &:hover {
      border-bottom-color: $primary; } } }

.bd-anchor-title {
  padding-top: 1.5rem;
  position: relative;
  @include until($widescreen) {
    padding-left: 2rem; } }

.bd-anchor-link {
  position: absolute;
  right: calc(100% + 1rem);
  top: 1.5rem;
  @include until($widescreen) {
    left: 0;
    right: auto; } }

.has-text-orange {
  color: $orange !important; }

.has-text-purple {
  color: $purple !important; }

.has-text-bootstrap {
  color: $bootstrap !important; }

.has-text-patreon {
  color: $patreon !important; }

.bd-emoji {
  margin-right: 0.5em;
  margin-top: 2px; }

.bd-emoji-bis {
  font-size: 1.25em;
  vertical-align: middle; }

$notification-background-color: v("background") !default;
$notification-radius: v("radius") !default;
$notification-padding: 1.25rem 2.5rem 1.25rem 1.5rem !default;

.bd-notification {
  background-color: $background;
  border-radius: $radius;
  color: $text-light;
  font-weight: $weight-semibold;
  padding: 1.25rem 0;
  position: relative;
  text-align: center;
  .title,
  .subtitle,
  .content,
  strong {
    color: currentColor; }
  code,
  pre {
    background-color: rgba($scheme-invert, 0.2);
    border-radius: $radius;
    color: $scheme-main; }
  pre code {
    background-color: transparent; }
  // Colors
  @each $name, $pair in $colors {
    $color: nth($pair, 1);
    $color-invert: nth($pair, 2);
    &.is-#{$name} {
      background-color: $color;
      color: $color-invert; } } }

.bd-icon-size .icon {
  background-color: $yellow; }

.bd-mwb-table {
  td {
    vertical-align: middle;
    img {
      vertical-align: middle; } } }
