#github {
  color: $github;
  border-color: $github;
  &:hover {
    background: $github;
    border-color: $github;
    color: $scheme-main; } }

.bd-tw-button {
  background-color: $twitter;
  color: $scheme-main;
  border-color: transparent !important;
  &:hover {
    background-color: darken($twitter, 2.5%);
    color: $scheme-main; }
  &:active,
  &:focus {
    background-color: darken($twitter, 5%);
    color: $scheme-main; } }

#moreDropdown {
  .navbar-item {
    padding-left: 3em;
    position: relative; }
  .icon {
    left: 1rem;
    position: absolute;
    text-align: center;
    top: 0.375rem;
    width: 1em; } }

#moreDropdownBootstrap {
  .icon {
    color: $bootstrap; } }

#moreDropdownPatreon {
  .icon {
    color: $patreon; } }

@include desktop {
  #blogDropdown {
    width: 17rem;
    .navbar-item {
      white-space: normal; } }
  #moreDropdown {
    width: 14rem;
    .navbar-item {
      padding-right: 1rem;
      white-space: normal;
      .level {
        flex-grow: 1; } } } }
