// $tw-black: #1c2022
$tw-black: $text-strong;
$tw-blue: #2b7bb9;
$tw-grey: #697882;
$tw-border: #e1e8ed;

.bd-tws-home {
  background-color: $scheme-main-bis; }

.bd-tw {
  background-color: $scheme-main-bis;
  border-radius: $radius-large;
  color: $tw-grey;
  flex-shrink: 0;
  font-family: Helvetica, Roboto, "Segoe UI", Calibri, sans-serif;
  font-size: 16px;
  max-width: 420px;
  padding: 20px;
  text-align: left;
  &.bd-is-white {
    background-color: $scheme-main; } }

.bd-tw-header {
  align-items: stretch;
  display: flex;
  justify-content: flex-start; }

.bd-tw-author {
  align-items: center;
  color: $tw-black;
  display: flex;
  line-height: 1.2;
  &:hover {
    color: $tw-blue; } }

.bd-tw-avatar {
  flex-shrink: 0;
  height: 36px;
  margin-right: 9px;
  width: 36px;
  img {
    border-radius: 290486px;
    display: block;
    height: 36px;
    width: 36px; } }

.bd-tw-fullname {
  color: $text-strong;
  display: block;
  font-size: 16px;
  font-weight: 700; }

.bd-tw-username {
  color: $tw-grey;
  display: block;
  font-size: 14px; }

.bd-tw-content {
  color: $tw-black;
  font-size: 16px;
  line-height: 1.4;
  margin-top: 14px;
  a {
    color: $tw-blue; }
  .Emoji {
    height: 1.25em;
    padding: 0 .05em 0 .1em;
    vertical-align: -.2em;
    width: 1.25em; } }

.bd-tw-date {
  font-size: 14px;
  line-height: 1.4;
  margin-top: 3.2px;
  a {
    color: $tw-grey;
    &:hover {
      color: $tw-blue; } } }

.Tweet-actions {
  align-items: center;
  display: flex;
  margin-top: 8.4px; }

.Tweet-action {
  margin-right: 1.25rem; }

