  .TweetAction-stat {
    color: #17BF63; } }

.Tweet-action--heart:hover {
  .TweetAction-stat {
    color: #E0245E; } }

.TweetAction-icon {
  background-size: contain;
  height: 20px;
  width: 20px; }

.TweetAction-stat {
