@include mobile {
  .bd-tws-home {
    padding: 1.5rem;
    .bd-tw:not(:last-child) {
      margin-bottom: 1.5rem; } } }

@include tablet {
  .bd-tws-home {
    padding: 3rem;
    .bd-tw + .bd-tw {
      margin-top: 1.5rem; } } }

@include until($desktop) {
  .bd-tws-love {
    .bd-tw:not(:last-child) {
      margin-bottom: 1.5rem; } } }

@include desktop {
  .bd-tws-home {
    min-height: 595px; }
  .bd-tws-love {
    display: flex;
    flex-wrap: wrap;
    margin: -0.75rem;
    .bd-tw {
      margin: 0.75rem;
      width: calc(33.3333% - 1.5rem); } } }

@include widescreen {
  .bd-tws-home {
    min-height: 653px; } }

@include fullhd {
  .bd-tws-home {
    min-height: 632px; } }

.twitter-tweet:not(.twitter-tweet-rendered) {
  background-color: $scheme-main;
  border: 1px solid $tw-border;
  border-radius: 5px;
  color: $tw-grey;
  font-size: 14px;
  padding: 20px 20px 11.6px;
  a {
    color: currentColor;
    &:hover {
      text-decoration: underline; } }
  p {
    color: $tw-black;
    font-size: 16px;
    margin-bottom: 3.2px;
    a {
      color: $tw-blue; } } }

.twitter-tweet-rendered {
  border: none;
  border-radius: 0;
  margin: 0 !important;
  padding: 0 !important; }
