.bd-callout {
  @include block;
  background-color: $background;
  border-radius: $radius;
  padding: 1.25rem 2.5rem 1.25rem 1.5rem;
  position: relative;
  // Colors
  @each $name, $pair in $colors {
    $color: nth($pair, 1);
    $color-invert: nth($pair, 2);
    &.is-#{$name} {
      background-color: $color;
      color: $color-invert; } } }
