.bd-example,
.bd-structure,
.bd-snippet {
  border: 2px solid $background;
  position: relative;
  &::before {
    background: $yellow;
    border-radius: $radius-small $radius-small 0 0;
    bottom: 100%;
    color: $yellow-invert;
    content: "Example";
    display: inline-block;
    font-size: 7px;
    font-weight: bold;
    left: -1px;
    letter-spacing: 1px;
    margin-left: -1px;
    padding: 3px 5px;
    position: absolute;
    text-transform: uppercase;
    vertical-align: top; }
  @include tablet {
    &.is-fullwidth {
      border-left: none;
      border-right: none;
      padding: 0; } } }

.bd-example,
.bd-structure {
  padding: 1.5rem;
  &:not(:first-child) {
    margin-top: 2rem; }
  &:not(:last-child) {
    margin-bottom: 1.5rem; } }

.bd-example {
  &.bd-is-clipped {
    border-top-right-radius: $radius-large; }
  & + .highlight {
    border: 1px solid $yellow;
    border-radius: 0 0 $radius $radius;
    border-top: none;
    margin-top: -1.5rem;
    &:not(:last-child) {
      margin-bottom: 1.5rem; } } }

// Snippet
.bd-snippet {
  @include block;
  margin-top: 2rem;
  position: relative;
  &::before {
    content: "Snippet";
    align-items: stretch;
    display: flex; } }

.bd-snippet-preview {
  padding: 1.5rem; }

.bd-snippet-code.bd-is-more.bd-is-more-clipped {
  .highlight {
    height: 2em;
    overflow: hidden;
    pre {
      overflow: hidden; }
    .bd-show {
      display: flex; } } }

.bd-snippet.bd-is-horizontal {
  border-radius: $radius-large;
  border-top-left-radius: 0;
  .bd-snippet-code {
    .highlight {
      border-top-left-radius: 0;
      border-top-right-radius: 0; }
    &.bd-is-more.bd-is-more-clipped {
      .highlight {
        pre {
          padding-top: 0.325rem; } } } } }

.bd-snippet.bd-is-clipped {
  .bd-snippet-code {
    overflow: auto; } }

.bd-snippet.bd-is-fullwidth {
  border-radius: 0;
  border-width: 1px 0; }

@include desktop {
  .bd-snippet.bd-is-vertical {
    align-items: stretch;
    border-radius: $radius-large;
    border-top-left-radius: 0;
    display: flex;
    justify-content: center;
    .bd-snippet-preview,
    .bd-snippet-code {
      width: 50%; }
    .bd-snippet-code {
      &,
      .highlight,
      .highlight pre {
        align-items: stretch;
        border-radius: 0 $radius-large $radius-large 0;
        display: flex;
        flex-direction: column; }
      .highlight,
      .highlight pre,
      .highlight .language-html {
        flex-grow: 1; }
      .highlight pre {
        white-space: pre; } }
    &.bd-is-one-fifth {
      .bd-snippet-preview {
        align-items: center;
        display: flex;
        justify-content: center;
        width: calc(128px + 3rem); }
      .bd-snippet-code {
        width: calc(100% - 128px - 3rem); } }
    &.bd-is-two-fifths {
      .bd-snippet-preview {
        align-items: center;
        display: flex;
        justify-content: center;
        width: calc(256px + 3rem); }
      .bd-snippet-code {
        width: calc(100% - 256px - 3rem); } }
    &.bd-is-one-third {
      .bd-snippet-preview {
        width: 33.3333%; }
      .bd-snippet-code {
        width: 66.6666%; } } } }

// Highlight
.highlight.bd-is-hovering {
  border-radius: 2px;
  box-shadow: 0 0 0 2px $yellow; }

.highlight pre {
  max-height: 320px;
  margin-bottom: 0 !important; }

.highlight-full,
#navbarJsExample {
  &:not(:last-child) {
    margin-bottom: 1.5rem; }
  .highlight pre {
    max-height: none; } }

$structure: $danger;
$structure-invert: $danger-invert;

.bd-structure {
  border-color: $structure;
  border-radius: $radius;
  padding: 1.5rem;
  &::before {
    background: $structure;
    color: $structure-invert;
    content: "Structure"; } }

.bd-structure-item {
  position: relative;
  &::before {
    @include overlay;
    background: rgba($scheme-invert, 0.7);
    background: $background;
    border: 1px solid $border;
    content: "";
    display: block;
    z-index: 1; }
  &::after {
    @include overlay;
    align-items: center;
    content: attr(title);
    display: flex;
    font-family: $family-monospace;
    font-size: 11px;
    justify-content: center;
    padding: 3px 5px;
    z-index: 2; }
  &.bd-is-structure-container {
    padding: 1.5rem 0.75rem 0.75rem;
    &::after {
      align-items: flex-start;
      justify-content: flex-start;
      padding: 0.5rem 0.75rem; } } }

.highlight {
  position: relative;
  .bd-copy,
  .bd-expand {
    background: none;
    border: none;
    color: $text;
    cursor: pointer;
    font-size: 0.625rem;
    outline: none;
    padding-bottom: 0;
    padding-top: 0;
    position: absolute;
    right: 0.25rem;
    top: 0.25rem;
    &:hover {
      background-color: $yellow;
      color: $yellow-invert; }
    &:focus,
    &:active {
      box-shadow: none; } }
  .bd-expand {
    right: 45px; }
  .bd-show {
    @include overlay;
    align-items: center;
    background-color: rgba($background, 0.7);
    border: none;
    color: rgba(#000, 0.5);
    cursor: pointer;
    display: none;
    font-size: $size-small;
    justify-content: center;
    width: 100%;
    strong {
      color: currentColor;
      font-weight: $weight-semibold; }
    &:hover {
      background-color: rgba($yellow, 0.8);
      color: rgba(#000, 0.7); } }
  @include tablet {
    pre {
      white-space: pre-wrap; } } }

@include tablet {
  .section:not(.is-fullwidth) > .bd-example:not(.is-fullwidth) {
    margin-left: 1.5rem;
    margin-right: 1.5rem;
    & + .highlight {
      margin-left: 1.5rem;
      margin-right: 1.5rem; } } }

.section.is-fullwidth {
  padding: 0 !important;
  .bd-snippet {
    border-left: none;
    border-radius: 0;
    border-right: none; }
  .bd-example {
    border-left: none;
    border-radius: 0;
    border-right: none;
    padding: 0;
    & + .highlight {
      border-left: none;
      border-radius: 0;
      border-right: none; } } }
