$section-padding: 3rem 1.5rem !default;
$section-padding-medium: 9rem 1.5rem !default;
$section-padding-large: 18rem 1.5rem !default;

@include vRegister(section-padding,$section-padding);
@include vRegister(section-padding-medium,$section-padding-medium);
@include vRegister(section-padding-large,$section-padding-large);

.section {
  padding: v("section-padding");
  // Responsiveness
  @include desktop {
    // Sizes
    &.is-medium {
      padding: v("section-padding-medium"); }
    &.is-large {
      padding: v("section-padding-large"); } } }
