@mixin defineVariable($var, $value) {
  #{varName($var)}: #{$value}; }

@if variable-exists(themeable) {
  @if $themeable == "any" {
     :root, [data-theme=bulma] {
      @each $name in $used_vars {
        @include defineVariable($name, map_get($registered_vars, $name)); } } }
  @else {
    @each $theme, $vars in $css_vars {
      $selector: "[data-theme=#{$theme}]";
      @if $theme == "default" {
        $selector: ":root #{$selector}"; }
      #{$selector} {
        @each $name, $value in $vars {
          @include defineVariable($name, map_get($registered_vars, $name)); } } } } }
