$file-border-color: v("border") !default;
$file-radius: v("radius") !default;

$file-cta-background-color: v("scheme-main-ter") !default;
$file-cta-color: v("text") !default;
$file-cta-hover-color: v("text-strong") !default;
$file-cta-active-color: v("text-strong") !default;

$file-name-border-color: v("border") !default;
$file-name-border-style: solid !default;
$file-name-border-width: 1px 1px 1px 0 !default;
$file-name-max-width: 16em !default;

@include vRegister(file-radius,$file-radius);
@include vRegister(file-cta-background-color,$file-cta-background-color);
@include vRegister(file-cta-hover-color,$file-cta-hover-color);
@include vRegister(file-name-border-color,$file-name-border-color);
@include vRegister(file-cta-active-color,$file-cta-active-color);
@include vRegister(file-border-color,$file-border-color);
@include vRegister(file-cta-color,$file-cta-color);
@include vRegister(file-name-border-style,$file-name-border-style);
@include vRegister(file-name-border-width,$file-name-border-width);
@include vRegister(file-name-max-width,$file-name-max-width);

.file {
  @extend %unselectable;
  align-items: stretch;
  display: flex;
  justify-content: flex-start;
  position: relative;
  // Colors
  @each $name in $colors-list {
    $color: v($name);
    $color-invert: v($name+"-invert");
    &.is-#{$name} {
      .file-cta {
        background-color: $color;
        border-color: transparent;
        color: $color-invert; }
      &:hover,
      &.is-hovered {
        .file-cta {
          background-color: vDarken($name,  2.5%);
          border-color: transparent;
          color: $color-invert; } }
      &:focus,
      &.is-focused {
        .file-cta {
          border-color: transparent;
          box-shadow: 0 0 0.5em vAlphaChange($name, 0.25);
          color: $color-invert; } }
      &:active,
      &.is-active {
        .file-cta {
          background-color: vDarken($name, 5%);
          border-color: transparent;
          color: $color-invert; } } } }
  // Sizes
  &.is-small {
    font-size: v("size-small"); }
  &.is-medium {
    font-size: v("size-medium");
    .file-icon {
      .fa {
        font-size: 21px; } } }
  &.is-large {
    font-size: v("size-large");
    .file-icon {
      .fa {
        font-size: 28px; } } }
  // Modifiers
  &.has-name {
    .file-cta {
      border-bottom-right-radius: 0;
      border-top-right-radius: 0; }
    .file-name {
      border-bottom-left-radius: 0;
      border-top-left-radius: 0; }
    &.is-empty {
      .file-cta {
        border-radius: v("file-radius"); }
      .file-name {
        display: none; } } }
  &.is-boxed {
    .file-label {
      flex-direction: column; }
    .file-cta {
      flex-direction: column;
      height: auto;
      padding: 1em 3em; }
    .file-name {
      border-width: 0 1px 1px; }
    .file-icon {
      height: 1.5em;
      width: 1.5em;
      .fa {
        font-size: 21px; } }
    &.is-small {
      .file-icon .fa {
        font-size: 14px; } }
    &.is-medium {
      .file-icon .fa {
        font-size: 28px; } }
    &.is-large {
      .file-icon .fa {
        font-size: 35px; } }
    &.has-name {
      .file-cta {
        border-radius: v("file-radius") v("file-radius") 0 0; }
      .file-name {
        border-radius: 0 0 v("file-radius") v("file-radius");
        border-width: 0 1px 1px; } } }
  &.is-centered {
    justify-content: center; }
  &.is-fullwidth {
    .file-label {
      width: 100%; }
    .file-name {
      flex-grow: 1;
      max-width: none; } }
  &.is-right {
    justify-content: flex-end;
    .file-cta {
      border-radius: 0 v("file-radius") v("file-radius") 0; }
    .file-name {
      border-radius: v("file-radius") 0 0 v("file-radius");
      border-width: 1px 0 1px 1px;
      order: -1; } } }

.file-label {
  align-items: stretch;
  display: flex;
  cursor: pointer;
  justify-content: flex-start;
  overflow: hidden;
  position: relative;
  &:hover {
    .file-cta {
      background-color: vDarken("file-cta-background-color",  2.5%);
      color: v("file-cta-hover-color"); }
    .file-name {
      border-color: vDarken("file-name-border-color",  2.5%); } }
  &:active {
    .file-cta {
      background-color: vDarken("file-cta-background-color",  5%);
      color: v("file-cta-active-color"); }
    .file-name {
      border-color: vDarken("file-name-border-color",  5%); } } }

.file-input {
  height: 100%;
  left: 0;
  opacity: 0;
  outline: none;
  position: absolute;
  top: 0;
  width: 100%; }

.file-cta,
.file-name {
  @extend %control;
  border-color: v("file-border-color");
  border-radius: v("file-radius");
  font-size: 1em;
  padding-left: 1em;
  padding-right: 1em;
  white-space: nowrap; }

.file-cta {
  background-color: v("file-cta-background-color");
  color: v("file-cta-color"); }

.file-name {
  border-color: v("file-name-border-color");
  border-style: v("file-name-border-style");
  border-width: v("file-name-border-width");
  display: block;
  max-width: v("file-name-max-width");
  overflow: hidden;
  text-align: left;
  text-overflow: ellipsis; }

.file-icon {
  align-items: center;
  display: flex;
  height: 1em;
  justify-content: center;
  margin-right: 0.5em;
  width: 1em;
  .fa {
    font-size: 14px; } }
