.bd-main {
  overflow: hidden;
  position: relative; }

.bd-figure {
  @include block;
  align-items: center;
  border: 2px solid $scheme-main-ter;
  border-radius: $radius;
  display: flex;
  flex-direction: column;
  text-align: center;
  img {
    margin: 1em; }
  figcaption {
    align-self: stretch;
    background-color: $scheme-main-ter;
    color: $text-light;
    font-size: $size-small;
    padding: 1em; } }

// Lead

.bd-lead {
  position: relative; }

.bd-breadcrumb {
  display: flex;
  justify-content: space-between;
  margin-bottom: $main-spacing;
  .breadcrumb:not(:last-child) {
    margin-bottom: 0; } }

.bd-prev-next {
  align-items: flex-start;
  color: $border-hover;
  display: none;
  flex-grow: 0;
  flex-shrink: 0;
  margin-left: 0.5rem;
  text-align: center;
  a,
  span {
    border-radius: $radius;
    width: 1.5rem; }
  a {
    &:hover {
      background-color: $background; } } }

.bd-prev-next-bis {
  border-top: 2px solid $background;
  display: flex;
  justify-content: space-between;
  margin-top: $main-spacing;
  a {
    margin-top: $main-spacing / 2; } }

.bd-prev-next-bis-previous {
  margin-right: auto; }

.bd-prev-next-bis-next {
  margin-left: auto; }

.bd-header {
  margin-bottom: $main-spacing;
  .subtitle {
    color: $text-light;
    strong {
      color: currentColor; } }
  #meta {
    margin-top: -0.5rem; } }

.bd-tabs {
  margin-bottom: $main-spacing; }

@include mobile {
  .bd-tabs {
    margin-left: -1.5rem;
    margin-right: -1.5rem;
    .tabs {
      ul {
        flex-wrap: nowrap; } } } }

@include tablet {
  .bd-tabs {
    .tabs {
      overflow: visible;
      ul,
      a {
        border-bottom-color: $scheme-main-ter;
        border-bottom-width: 2px; }
      a {
        margin-bottom: -2px; }
      li:not(.is-active) a:hover {
          border-bottom-color: $border; }
      ul {
        flex-wrap: wrap;
        max-width: 100%; } } } }

.bd-content {
  hr:first-child {
    display: none;
    & + .bd-anchor-title {
      padding-top: 0;
      .bd-anchor-link {
        top: 0; } } } }

// Side

.bd-side,
.bd-side-background {
  background-color: $scheme-main-bis; }

.bd-side {
  position: relative; }

.bd-side-background {
  bottom: 0;
  left: 50%;
  position: absolute;
  right: 0;
  top: 0; }

%bd-list {
  font-size: 0.875rem;
  li {
    &:not(:last-child) {
      margin-bottom: 0.5em; }
    &.is-past {
      a {
        color: $border-hover; } }
    &.is-current {
      a {
        color: $link; } } }
  a {
    color: $text-light;
    &:hover {
      color: $link; } } }

.bd-category {
  a {
    &:hover {
      color: $link; } }
  &:not(:last-child) {
    margin-bottom: 0.5rem; }
  &.is-active {
    .bd-category-toggle {
      .icon {
        transform: rotate(180deg); } }
    .bd-category-list {
      display: block; } } }

.bd-category-header {
  position: relative; }

.bd-category-toggle {
  @include overlay;
  align-items: center;
  color: $border-hover;
  display: flex;
  justify-content: flex-end;
  .icon {
    font-size: 0.75rem;
    transform-origin: center;
    transition-duration: $speed;
    transition-property: transform; } }

.tag.bd-mini-tag {
  border-radius: 2px !important;
  font-family: Verdana, sans-serif;
  font-size: 0.5rem;
  font-weight: bold;
  height: 2.25em;
  vertical-align: text-bottom; }

.bd-category-name {
  color: $text-strong;
  position: relative;
  &.is-active {
    color: $link; }
  .bd-mini-tag {
    margin-left: 0.5em; } }

.bd-category-list {
  @extend %bd-list;
  display: none;
  padding: 0.5rem; }

.bd-anchors-reference {
  height: 1px; }

.bd-anchors {
  display: none;
  max-width: $sidebar-width;
  padding-top: calc(1.5rem - 1px);
  @include tablet {
    &.is-active {
      display: block; }
    &.is-pinned {
      position: fixed;
      top: 0; } } }

.bd-anchors-title {
  color: $border-hover;
  font-size: 0.875rem;
  font-weight: $weight-semibold;
  margin-bottom: 0.5rem; }

.bd-anchors-list {
  @extend %bd-list;
  li {
    &:last-child {
      margin-top: 1em; } }
  a {
    color: $text-strong; } }

@include touch {
  .bd-lead,
  .bd-side {
    padding: 1.5rem; }
  .bd-header-carbon {
    margin-top: 1.5rem; } }

@include touch {
  .bd-side-background {
    display: none; } }

@include desktop {
  .bd-duo {
    display: flex; }
  .bd-lead {
    background-color: $scheme-main;
    overflow: hidden;
    flex-grow: 1;
    flex-shrink: 1; }
  .bd-prev-next {
    display: flex; }
  .bd-side {
    flex: 0 0 calc(#{$sidebar-width} + 1.5rem); } }

@include desktop {
  .bd-main {
    padding: 0 3rem; }
  .bd-lead {
    margin-left: -3rem;
    padding: 3rem;
    &:last-child {
      margin-right: -3rem; } }
  .bd-header {
    align-items: center;
    display: flex;
    min-height: 140px; }
  .bd-header-titles {
    flex-grow: 1;
    flex-shrink: 1; }
  .bd-header-carbon {
    flex-grow: 0;
    flex-shrink: 0;
    margin: -15px 0 -15px ($main-spacing * 2);
    width: $carbon-width; }
  .bd-side {
    padding: 3rem 0 3rem 1.5rem; } }

@include widescreen {
  .bd-header-carbon {
    margin-left: ($main-spacing * 3); } }

@include fullhd {
  .bd-header-carbon {
    margin-left: ($main-spacing * 4); } }
